.top-bar {
  max-width: 100vw;
  background-color: #164d80;
  padding: 8px 12px;
  color: #ffffff;
  display: flex;
  flex-direction: row;
  font-size: 10px;
  align-items: center;
}

.item > p {
  font-size: 12px;
  margin-bottom: 4px;
}

.top-bar > .buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.top-bar button {
  background-color: transparent;
  outline: none;
  border: none;
  padding: 5px 8px;
  margin: 0 4px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  color: #ffffff;
  transition: 300ms all ease-in-out;
}

.top-bar button:not(:disabled):hover {
  background-color: rgba(0, 0, 0, 0.2);
}

button > img {
  width: 16px;
  margin-bottom: 4px;
}

.top-bar button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.bold {
  font-weight: bold;
}

