.answer-sheet {
  flex: 1;
  max-height: 100%;
  overflow-y: auto;
  padding: 20px;
}

.container {
  background-color: #eeeeee;
  padding: 24px;
  border: 1px solid #d4d4d4;
  border-radius: 5px;
}

.container > h2 {
  font-weight: bold;
  font-size: larger;
  margin-bottom: 16px;
  color: #164d80;
}

.container p {
  font-size: medium;
  line-height: 1.7;
  white-space: pre-line;
}

ul {
  list-style-type: upper-roman;
}
ul > li {
  display: flex;
  align-items: center;
  padding: 8px;
}
li > span {
    font-weight: bold;
}

li > input {
  margin:0 8px;
}

li > label {
  font-size: medium;
}
