.scroll-container {
  height: 100%;
  width: 180px;
  font-weight: 600;
  background-color: #ffffff;
  overflow-y: auto;
  border-right: 1px solid rgba(140, 172, 194, 0.5);
}

ol > li {
  background-color: rgba(140, 172, 194, 0.4);
  font-size: smaller;
  padding: 12px 8px;
  text-align: center;
  cursor: pointer;
  transition: 300ms all ease-in-out;
}

ol > li.active {
    background-color: #164d80;
    color: white;
}

ol > li:not(.active):nth-of-type(even) {
  background-color: rgba(140, 172, 194, 0.8);
}

@media only screen and (max-width: 600px) {
  .scroll-container {
    width: 90px;
  }

  .mobile-hidden {
    display: none;
  }
}
