#app {
  width: 100vw;
  height: 100vh;
  position: relative;
  min-width: 360px;
}

#content {
  display: relative;
  height: calc(100vh - 116px);
  background-color: #ffffff;
  display: flex;
  overflow: hidden;
}

body {
  overflow: hidden;
}

.note {
  position: absolute;
  width: 460px;
  bottom: 64px;
  right: 16px;
  background-color: #ffffff;
  border: 1px solid #164d80;
  border-radius: 5px;
}

.note > .header {
  text-align: center;
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  padding: 16px 4px;
  background-color: #164d80;
}

.header > h3 {
  flex: 1;
  color: #ffffff;
  font-size: 18px;
}

.closeBtn {
  position: absolute;
  right: 4px;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  color: #164d80;
  font-size: large;
  background-color: #ffffff;
  outline: none;
  border: none;
}

.text-area {
  padding: 16px;
}
.text-area > textarea {
  resize: none;
  width: 100%;
  border: 2px solid #d2d2d2;
  border-radius: 5px;
  padding: 6px 8px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.note > .footer {
  display: flex;
  justify-content: space-between;
  padding: 16px;
}

.footer > button {
  padding: 6px;
  border: 1px solid #164d80;
  border-radius: 5px;
  font-weight: 500;
  font-size: 12px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal > .content {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 5px;
  width: 480px;
  animation: slideInUp 400ms ease-in;
}

.modal > .content > h3 {
  font-size: large;
  font-weight: 600;
  margin: 16px 0;
}

.modal > .content > p {
  font-size: small;
  line-height: 1.4;
  margin-bottom: 24px;
}

.modal .footer {
  display: flex;
  justify-content: flex-end;
}
.modal .footer button {
  margin: 0 4px;
  font-size: small;
}

.modal .footer button:last-of-type {
  background-color: #164d80;
  color: #ffffff;
}

.handle {
  cursor: move;
}
.react-grid-item {
  touch-action: initial !important;
}
.react-grid-item .drag-handle {
  touch-action: none;
}
.slideIn {
  animation: slideIn 500ms ease-in-out;
}

._setting-container {
  padding: 16px;
  background-color: #fcfcfc;
  border: 1px solid #164d80;
  position: absolute;
  bottom: 58px;
  right: 16px;
  -webkit-box-shadow: 0px 4px 19px 1px rgba(0, 0, 0, 0.34);
  -moz-box-shadow: 0px 4px 19px 1px rgba(0, 0, 0, 0.34);
  box-shadow: 0px 4px 19px 1px rgba(0, 0, 0, 0.34);
  animation: fadeIn 600ms ease-in;
}

._setting-container > h3 {
  font-size: large;
  text-align: center;
  font-weight: 600;
  color: #164d80;
  margin-bottom: 20px;
}

._setting-container > ._setting-section {
  margin: 12px 0;
}

._setting-section > h4 {
  font-size: small;
  color: #acacac;
  margin-bottom: 8px;
}

._setting-section > ._setting-options {
  background-color: #ffffff;
  border: 1px #d2d2d2 solid;
  width: 100%;
  border-radius: 5px;
}

._setting-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

ul > ._setting-item:not(:last-of-type) {
  border-bottom: 1px #d2d2d2 solid;
}
._setting-item > p {
  flex: 1;
  font-size: small;
  color: #686868;
  padding: 2px 0;
}

._setting-container button {
  height: 25px;
  width: 25px;
  margin: 0 2px;
  border-radius: 28px;
  outline: none;
  border: none;
  background-color: #164d80;
  color: #ffffff;
  display: flex;
  font-weight: 700 !important;
  align-items: center;
  justify-content: center;
}
._lab-container {
  position: absolute;
  top: 58px;
  right: 0;
  bottom: 58px;
  overflow-x: auto;
  background-color: #ffffff;
  border-left: 1px solid #164d80;
  width: 480px;
}

._lab-container table {
  border-collapse: collapse;
  width: 100%;
}

._lab-container table th {
  font-weight: bold;
  color: #164d80;
}

._lab-container table > thead {
  border-bottom: 1px solid #164d80;
}
._lab-container table th,
._lab-container table td {
  text-align: left;
  padding: 16px;
  border: 1px solid #ddd;
}

._closeBtn {
  position: absolute;
  top: -10px;
  right: -10px;
}
@media screen and (max-width: 600px) {
  ._hidden,
  .top-bar button > span {
    display: none !important;
  }

  .top-bar button {
    padding: 11px 8px;
  }

  .modal > .content,
  .note,
  ._lab-container {
    width: 90% !important;
  }
  .note .header > button {
    height: 30px;
    width: 30px;
    border-radius: 30px;
    touch-action: auto !important;
  }

  .modal .footer button {
    padding: 8px 12px !important;
  }
}

@keyframes slideIn {
  0% {
    transform: translateX(100%);
  }
  70% {
    transform: translateX(-20%);
  }
  100% {
    transform: translateX(0%);
  }
}

@keyframes slideInUp {
  0% {
    transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
