html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

*{
  box-sizing: border-box;
}
/* make sure to set some focus styles for accessibility */
:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type="search"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  box-sizing: content-box;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */

audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
  max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */

audio:not([controls]) {
  display: none;
  height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */

[hidden] {
  display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */

html {
  font-size: 100%; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
  -ms-text-size-adjust: 100%; /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */

a:focus {
  outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */

a:active,
a:hover {
  outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */

img {
  border: 0; /* 1 */
  -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */

figure {
  margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */

form {
  margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */

legend {
  border: 0; /* 1 */
  padding: 0;
  white-space: normal; /* 2 */
  *margin-left: -7px; /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */

button,
input,
select,
textarea {
  font-size: 100%; /* 1 */
  margin: 0; /* 2 */
  vertical-align: baseline; /* 3 */
  *vertical-align: middle; /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */

button,
input {
  line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */

button,
select {
  text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */

button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button; /* 2 */
  cursor: pointer; /* 3 */
  *overflow: visible; /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
  cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
  *height: 13px; /* 3 */
  *width: 13px; /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */

input[type="search"] {
  -webkit-appearance: textfield; /* 1 */ /* 2 */
  box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */

textarea {
  overflow: auto; /* 1 */
  vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
button,
input,
select,
textarea {
  color: #222;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

img {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

body,
html {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.top-bar {
  max-width: 100vw;
  background-color: #164d80;
  padding: 8px 12px;
  color: #ffffff;
  display: flex;
  flex-direction: row;
  font-size: 10px;
  align-items: center;
}

.item > p {
  font-size: 12px;
  margin-bottom: 4px;
}

.top-bar > .buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.top-bar button {
  background-color: transparent;
  outline: none;
  border: none;
  padding: 5px 8px;
  margin: 0 4px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  color: #ffffff;
  transition: 300ms all ease-in-out;
}

.top-bar button:not(:disabled):hover {
  background-color: rgba(0, 0, 0, 0.2);
}

button > img {
  width: 16px;
  margin-bottom: 4px;
}

.top-bar button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.bold {
  font-weight: bold;
}


.scroll-container {
  height: 100%;
  width: 180px;
  font-weight: 600;
  background-color: #ffffff;
  overflow-y: auto;
  border-right: 1px solid rgba(140, 172, 194, 0.5);
}

ol > li {
  background-color: rgba(140, 172, 194, 0.4);
  font-size: smaller;
  padding: 12px 8px;
  text-align: center;
  cursor: pointer;
  transition: 300ms all ease-in-out;
}

ol > li.active {
    background-color: #164d80;
    color: white;
}

ol > li:not(.active):nth-of-type(even) {
  background-color: rgba(140, 172, 194, 0.8);
}

@media only screen and (max-width: 600px) {
  .scroll-container {
    width: 90px;
  }

  .mobile-hidden {
    display: none;
  }
}

#app {
  width: 100vw;
  height: 100vh;
  position: relative;
  min-width: 360px;
}

#content {
  display: relative;
  height: calc(100vh - 116px);
  background-color: #ffffff;
  display: flex;
  overflow: hidden;
}

body {
  overflow: hidden;
}

.note {
  position: absolute;
  width: 460px;
  bottom: 64px;
  right: 16px;
  background-color: #ffffff;
  border: 1px solid #164d80;
  border-radius: 5px;
}

.note > .header {
  text-align: center;
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  padding: 16px 4px;
  background-color: #164d80;
}

.header > h3 {
  flex: 1 1;
  color: #ffffff;
  font-size: 18px;
}

.closeBtn {
  position: absolute;
  right: 4px;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  color: #164d80;
  font-size: large;
  background-color: #ffffff;
  outline: none;
  border: none;
}

.text-area {
  padding: 16px;
}
.text-area > textarea {
  resize: none;
  width: 100%;
  border: 2px solid #d2d2d2;
  border-radius: 5px;
  padding: 6px 8px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.note > .footer {
  display: flex;
  justify-content: space-between;
  padding: 16px;
}

.footer > button {
  padding: 6px;
  border: 1px solid #164d80;
  border-radius: 5px;
  font-weight: 500;
  font-size: 12px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal > .content {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 5px;
  width: 480px;
  animation: slideInUp 400ms ease-in;
}

.modal > .content > h3 {
  font-size: large;
  font-weight: 600;
  margin: 16px 0;
}

.modal > .content > p {
  font-size: small;
  line-height: 1.4;
  margin-bottom: 24px;
}

.modal .footer {
  display: flex;
  justify-content: flex-end;
}
.modal .footer button {
  margin: 0 4px;
  font-size: small;
}

.modal .footer button:last-of-type {
  background-color: #164d80;
  color: #ffffff;
}

.handle {
  cursor: move;
}
.react-grid-item {
  touch-action: initial !important;
}
.react-grid-item .drag-handle {
  touch-action: none;
}
.slideIn {
  animation: slideIn 500ms ease-in-out;
}

._setting-container {
  padding: 16px;
  background-color: #fcfcfc;
  border: 1px solid #164d80;
  position: absolute;
  bottom: 58px;
  right: 16px;
  box-shadow: 0px 4px 19px 1px rgba(0, 0, 0, 0.34);
  animation: fadeIn 600ms ease-in;
}

._setting-container > h3 {
  font-size: large;
  text-align: center;
  font-weight: 600;
  color: #164d80;
  margin-bottom: 20px;
}

._setting-container > ._setting-section {
  margin: 12px 0;
}

._setting-section > h4 {
  font-size: small;
  color: #acacac;
  margin-bottom: 8px;
}

._setting-section > ._setting-options {
  background-color: #ffffff;
  border: 1px #d2d2d2 solid;
  width: 100%;
  border-radius: 5px;
}

._setting-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

ul > ._setting-item:not(:last-of-type) {
  border-bottom: 1px #d2d2d2 solid;
}
._setting-item > p {
  flex: 1 1;
  font-size: small;
  color: #686868;
  padding: 2px 0;
}

._setting-container button {
  height: 25px;
  width: 25px;
  margin: 0 2px;
  border-radius: 28px;
  outline: none;
  border: none;
  background-color: #164d80;
  color: #ffffff;
  display: flex;
  font-weight: 700 !important;
  align-items: center;
  justify-content: center;
}
._lab-container {
  position: absolute;
  top: 58px;
  right: 0;
  bottom: 58px;
  overflow-x: auto;
  background-color: #ffffff;
  border-left: 1px solid #164d80;
  width: 480px;
}

._lab-container table {
  border-collapse: collapse;
  width: 100%;
}

._lab-container table th {
  font-weight: bold;
  color: #164d80;
}

._lab-container table > thead {
  border-bottom: 1px solid #164d80;
}
._lab-container table th,
._lab-container table td {
  text-align: left;
  padding: 16px;
  border: 1px solid #ddd;
}

._closeBtn {
  position: absolute;
  top: -10px;
  right: -10px;
}
@media screen and (max-width: 600px) {
  ._hidden,
  .top-bar button > span {
    display: none !important;
  }

  .top-bar button {
    padding: 11px 8px;
  }

  .modal > .content,
  .note,
  ._lab-container {
    width: 90% !important;
  }
  .note .header > button {
    height: 30px;
    width: 30px;
    border-radius: 30px;
    touch-action: auto !important;
  }

  .modal .footer button {
    padding: 8px 12px !important;
  }
}

@keyframes slideIn {
  0% {
    transform: translateX(100%);
  }
  70% {
    transform: translateX(-20%);
  }
  100% {
    transform: translateX(0%);
  }
}

@keyframes slideInUp {
  0% {
    transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.answer-sheet {
  flex: 1 1;
  max-height: 100%;
  overflow-y: auto;
  padding: 20px;
}

.container {
  background-color: #eeeeee;
  padding: 24px;
  border: 1px solid #d4d4d4;
  border-radius: 5px;
}

.container > h2 {
  font-weight: bold;
  font-size: larger;
  margin-bottom: 16px;
  color: #164d80;
}

.container p {
  font-size: medium;
  line-height: 1.7;
  white-space: pre-line;
}

ul {
  list-style-type: upper-roman;
}
ul > li {
  display: flex;
  align-items: center;
  padding: 8px;
}
li > span {
    font-weight: bold;
}

li > input {
  margin:0 8px;
}

li > label {
  font-size: medium;
}

